import React, { Component } from "react";
import Accordion from "react-bootstrap/Accordion";

export default class Chapterscard extends Component {
  render() {
    return (
      <div>
        <div className="card border-warning">
          <div className="card-body">
            <h5 className="card-title">
              {this.props.data.chapter_number}. {this.props.data.name}
            </h5>
            <p className="card-text">
              {this.props.data.meaning.en} / {this.props.data.meaning.hi}
            </p>
            <p className="card-text">
              <Accordion>
                <Accordion.Item eventKey="0">
                  <Accordion.Header>Hindi Summary</Accordion.Header>
                  <Accordion.Body>
                    <b>{this.props.data.summary.hi}</b>
                  </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="1">
                  <Accordion.Header>English Summary</Accordion.Header>
                  <Accordion.Body>
                    <b>{this.props.data.summary.en}</b>
                  </Accordion.Body>
                </Accordion.Item>
              </Accordion>
              <br />
            </p>
            <p className="card-text text-center">
              <button className="btn btn-outline-danger ">Read Sholks</button>
            </p>
          </div>
        </div>
        <br />
      </div>
    );
  }
}
